import React from 'react';
import './App.css';
import Viewer from './components/viewer/Viewer';
import { Outline } from './types/types';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { QueryClient, QueryClientProvider } from 'react-query';




function App() {
  const [outline, setOutline] = useState<Outline | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // @ts-ignore
    const bookId = window.APP_BOOK_ID;

    setIsLoading(true);

    // Outline  
    axios.get(`/api/books/${bookId}/outline`)
    .then((response) => {
      if(response){
        setOutline(response.data);
        console.log("### outline data fetching success! ###", response.data);
        setIsLoading(false);
        return response.data;
      }
    })
    .catch((error) => {
      console.log(error);
    })
  }, []);


  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (!outline) {
    return <div>No data available.</div>;
  }


  const queryClient = new QueryClient({ 
    // React Query  QueryClient   
    // QueryClient React Query  , ,    
    //      .

    defaultOptions: {
      queries: {
        refetchOnMount: false, //  true,          
        refetchOnWindowFocus: false, //  true,             
        retry: false, //   ,    .
        staleTime: 1000 * 60 * 60 // 1
      }
    }
  });

  return (
    <QueryClientProvider client={queryClient}>
      <Viewer outline={outline}></Viewer>
    </QueryClientProvider>
  ); 
  // QueryClientProvider    useQuery, useMutation      , 
  // queryClient     .
  // Viewer         .
}

export default App;
